import Color from "../../res/colors";
import { SplashScreenElement, SplashScreenLogo } from "./style";

const SplashScreen = () => {
  const color = [
    Color.primary,
    Color.secondary,
    Color.danger,
    Color.warning,
  ].sort(() => 0.5 - Math.random());
  const initialColor = color.splice(0, 1)[0];

  return (
    <SplashScreenElement
      initial={{ backgroundColor: initialColor }}
      animate={{
        backgroundColor: [...color],
      }}
      transition={{
        backgroundColor: { duration: 2 },
      }}
    >
      <SplashScreenLogo />
    </SplashScreenElement>
  );
};

export default SplashScreen;
