import { useState } from "react";
import { useEffect } from "react";
import { FC } from "react";
import useFinishAnimSplashScreen from "../../hooks/useFinishAnimSplashScreen";
import { DisableBodyScroll, MainContent, MainElement } from "./style";

const Main: FC = ({ children }) => {
  const [isFinishAnimSplashScreen] = useFinishAnimSplashScreen();
  const [enableScroll, setEnableScroll] = useState(false);

  useEffect(() => {
    let scrollTimeout: NodeJS.Timeout;

    if (isFinishAnimSplashScreen) {
      scrollTimeout = setTimeout(() => {
        setEnableScroll(true);
      }, 500);
    }

    return () => {
      clearTimeout(scrollTimeout);
    };
  }, [isFinishAnimSplashScreen]);

  return (
    <MainElement
      initial={{ width: 0 }}
      animate={{ width: "initial" }}
      transition={{ delay: 3, duration: 1 }}
    >
      <DisableBodyScroll enable={enableScroll} />
      <MainContent>{children}</MainContent>
    </MainElement>
  );
};

export default Main;
