import { createContext, Dispatch, FC, useContext, useState } from "react";

const FinishAnimSplashScreenContext = createContext<
  [boolean, Dispatch<boolean>]
>([] as any);

export const FinishAnimSplashScreenProvider: FC = ({ children }) => {
  const [isFinish, setIsFinish] = useState<boolean>(false);

  return (
    <FinishAnimSplashScreenContext.Provider value={[isFinish, setIsFinish]}>
      {children}
    </FinishAnimSplashScreenContext.Provider>
  );
};

const useFinishAnimSplashScreen = () =>
  useContext(FinishAnimSplashScreenContext);

export default useFinishAnimSplashScreen;
