import { motion } from "framer-motion";
import styled from "styled-components";
import ViewPort from "../../../res/viewport";

export const SertificateElement = styled.div`
  display: grid;
  grid-gap: 0.5em;

  @media (min-width: ${ViewPort.medium}) {
    grid-template-columns: repeat(3, 1fr);
  }
`;

export const SertificateImage = styled(motion.img)`
  width: 100%;
  height: auto;
`;
