import { useAnimation } from "framer-motion";
import { useEffect } from "react";
import { FC } from "react";
import { useInView } from "react-intersection-observer";
import { SectionContent, SectionElement, SectionTitle } from "./style";

interface SectionProps {
  title: string;
}

const Section: FC<SectionProps> = ({ children, title }) => {
  const { ref, inView } = useInView();
  const sectionAnimControl = useAnimation();

  useEffect(() => {
    console.log(inView);
    if (inView) {
      sectionAnimControl.start({ opacity: 1, y: 0 });
    }
  }, [inView]);

  return (
    <SectionElement
      ref={ref}
      initial={{ opacity: 0, y: 50 }}
      transition={{ duration: 0.5, ease: "linear" }}
      animate={sectionAnimControl}
    >
      <SectionContent>
        <SectionTitle id={title}>{title}</SectionTitle>
        {children}
      </SectionContent>
    </SectionElement>
  );
};

export default Section;
