import { useEffect } from "react";
import { motion, useAnimation } from "framer-motion";
import useFinishAnimSplashScreen from "../../hooks/useFinishAnimSplashScreen";
import Button from "../button";
import {
  HeroDetailContainer,
  HeroElement,
  HeroImage,
  HeroImageContainer,
  HeroImageForAnimBottom,
  HeroText,
} from "./style";

const Hero = () => {
  const [isFinishAnimSplashScreen] = useFinishAnimSplashScreen();
  const emojiAnimControl = useAnimation();
  const imageBorderAnimControl = useAnimation();

  useEffect(() => {
    if (isFinishAnimSplashScreen) {
      emojiAnimControl.start({ rotate: [0, -45, 0], scale: [1, 1.5, 1] });
      imageBorderAnimControl.start({ height: "100%", width: "100%" });
    }
  }, [isFinishAnimSplashScreen]);

  return (
    <HeroElement>
      <HeroDetailContainer>
        <HeroText>
          Hello there{" "}
          <motion.span
            initial={{ display: "inline-block" }}
            animate={emojiAnimControl}
          >
            ðŸ¤ž
          </motion.span>
          , I'm a Full Stack enthusiast that made with TypeScript / Node.js /
          React.js
        </HeroText>
        <Button.Group>
          <Button href="mailto:fahri.ahmad@live.com">Email Me</Button>
          <Button href="./fahri_resume.pdf">Download Resume</Button>
        </Button.Group>
      </HeroDetailContainer>
      <HeroImageContainer>
        <HeroImageForAnimBottom
          animate={imageBorderAnimControl}
          transition={{ duration: 1 }}
        />
        <HeroImage src="./hero.jpg" alt="fahri" />
      </HeroImageContainer>
    </HeroElement>
  );
};

export default Hero;
