import { motion } from "framer-motion";
import styled from "styled-components";
import Color from "../../../res/colors";
import ViewPort, { ViewPortEm } from "../../../res/viewport";

export const HeroElement = styled.div`
  display: flex;
  flex-direction: column-reverse;
  max-width: ${ViewPortEm.large};
  padding: 2em;
  margin: 0 auto;
  box-sizing: border-box;
  margin-bottom: 2em;

  @media (min-width: ${ViewPort.medium}) {
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    padding-top: 6em;
    padding-bottom: 10em;
    gap: 2em;
  }
`;

export const HeroDetailContainer = styled.div``;

export const HeroText = styled.p`
  font-size: 1.3em;
  max-width: 24em;
  margin-top: 2em;
  margin-bottom: 1em;
`;

export const HeroImageContainer = styled.div`
  position: relative;
`;

export const HeroImage = styled.img`
  position: relative;
  width: 100%;

  @media (min-width: ${ViewPort.medium}) {
    width: 20em;
    height: 20em;
  }
`;

export const HeroImageForAnimBottom = styled(motion.div)`
  bottom: -0.8em;
  left: -0.8em;
  border: 0.1em solid ${Color.dark};
  border-right: 0;
  border-top: 0;
  position: absolute;
`;
