import { useEffect, useState } from "react";
import ViewPort from "../res/viewport";

const getWindowDimension = () => [window.innerWidth, window.innerHeight];

const getCurrentViewPort = (width: number) => {
  const viewPort = Object.values(ViewPort).map((v) => parseInt(v));
  const currentViewPort = viewPort.filter((v) => v <= width);
  return currentViewPort.length > 0
    ? currentViewPort[currentViewPort.length - 1]
    : viewPort[0];
};

const useViewPort = () => {
  const [windowDimensions, setWindowDimensions] = useState(getWindowDimension);
  const [viewPort, setViewPort] = useState(() =>
    getCurrentViewPort(windowDimensions[0])
  );
  const currentViewPort = getCurrentViewPort(windowDimensions[0]);

  useEffect(() => {
    window.addEventListener("resize", () =>
      setWindowDimensions(getWindowDimension())
    );
  }, []);

  useEffect(() => {
    setViewPort(currentViewPort);
  }, [currentViewPort]);

  return viewPort + "px";
};

export default useViewPort;
