import Section from "../section";
import { SertificateElement, SertificateImage } from "./style";

const Sertificate = () => {
  const firstSertif = {
    src: "./bangkit-sertif.png",
    alt: "Bangkit's Sertificate",
  };

  const secondSertif = {
    src: "./android-sertif.png",
    alt: "Menjadi Developer Android Expert's Sertificate",
  };

  const thirdSertif = {
    src: "./backend-sertif.png",
    alt: "Belajar Membuat Aplikasi Backend untuk Pemula's Sertificate",
  };

  return (
    <Section title="Sertificate">
      <SertificateElement>
        <SertificateImage {...firstSertif} />
        <SertificateImage {...secondSertif} />
        <SertificateImage {...thirdSertif} />
      </SertificateElement>
    </Section>
  );
};

export default Sertificate;
