import { AnchorHTMLAttributes, FC } from "react";
import { motion } from "framer-motion";
import styled from "styled-components";
import Color from "../../../res/colors";

export const ButtonElement: FC<
  AnchorHTMLAttributes<HTMLAnchorElement>
> = styled.a`
  position: relative;
  border: 0.1em solid ${Color.dark};
  background-color: transparent;
  padding: 0.8em 1.5em;
  text-decoration: none;
  color: inherit;
  z-index: 10;
  cursor: pointer;
`;

export const ButtonElementForAnim = styled(motion.div)`
  position: absolute;
  top: 0;
  left: 0;
  background-color: ${Color.primary};
  height: 100%;
`;

export const ButtonElementForAnimRight = styled(ButtonElementForAnim)`
  left: initial;
  right: 0;
  background-color: ${Color.secondary};
`;

export const ButtonElementGroup = styled.div`
  display: inline-flex;
  gap: 1em;
`;

export const ButtonText = styled.span`
  text-transform: uppercase;
  font-weight: bold;
  position: relative;
  z-index: 1;
`;
