import { AnchorHTMLAttributes, useState } from "react";
import { FC } from "react";
import {
  ButtonElement,
  ButtonElementForAnim,
  ButtonElementForAnimRight,
  ButtonElementGroup,
  ButtonText,
} from "./style";

interface IButton {
  Group: FC;
}

const Button: FC<AnchorHTMLAttributes<HTMLAnchorElement>> & IButton = ({
  children,
  ...props
}) => {
  const [btnHover, setBtnHover] = useState(false);

  return (
    <ButtonElement
      {...props}
      onMouseEnter={() => setBtnHover(true)}
      onMouseLeave={() => setBtnHover(false)}
    >
      <ButtonElementForAnimRight
        animate={btnHover ? { width: "100%" } : undefined}
        transition={{ duration: 0.25 }}
      />
      <ButtonElementForAnim
        animate={btnHover ? { width: "100%" } : undefined}
        transition={{ delay: 0.5, duration: 0.25 }}
      />
      <ButtonText>{children}</ButtonText>
    </ButtonElement>
  );
};

Button.Group = ({ children }) => {
  return <ButtonElementGroup>{children}</ButtonElementGroup>;
};

export default Button;
