import { motion } from "framer-motion";
import styled from "styled-components";
import ViewPort, { ViewPortEm } from "../../../res/viewport";

export const SectionElement = styled(motion.div)`
  overflow: hidden;

  @media (min-width: ${ViewPort.medium}) {
    & + & {
      margin-top: 5em;
    }
  }
`;

export const SectionContent = styled.div`
  max-width: ${ViewPortEm.large};
  margin: 0 auto;
  padding: 2em;
  padding-top: 0;
  overflow: hidden;
`;

export const SectionTitle = styled.h2`
  font-weight: normal;
  margin: 0;
  margin-bottom: 2em;
  text-align: center;
`;

export const SectionDividerHorizontalLine = () => {
  return (
    <svg
      width="100%"
      height="5em"
      viewBox="10 0 210 10"
      preserveAspectRatio="none"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
    >
      <motion.path
        initial={{ strokeDasharray: 1000, strokeDashoffset: 1000 }}
        animate={{ strokeDashoffset: 0 }}
        transition={{ delay: 3.5, duration: 2 }}
        d="M1 2.0699C153.806 20.4514 106.21 -4.27415 251 2.0699"
        stroke="black"
      />
    </svg>
  );
};
