import { motion } from "framer-motion";
import { FC } from "react";
import styled from "styled-components";
import Color from "../../../res/colors";
import ViewPort from "../../../res/viewport";

export const NavbarElement = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  overflow: hidden;
  padding: 1.6em 1.5em;

  @media (min-width: ${ViewPort.small}) {
    padding: 1.6em 2.5em;
  }

  @media (min-width: ${ViewPort.medium}) {
    padding: 1.6em 4em;
  }

  @media (min-width: ${ViewPort.large}) {
    padding: 1.6em 5.5em;
  }

  @media (min-width: ${ViewPort.xlarge}) {
    padding: 1.6em 6.25em;
  }
`;

export const NavbarImage = styled.img`
  width: 5em;
  height: 7.2em;
`;

export const NavbarNav = styled.nav`
  display: none;
  gap: 2em;

  @media (min-width: ${ViewPort.small}) {
    display: inline-flex;
  }
`;

const NavbarAnchor = styled(motion.a)`
  text-decoration: none;
  color: inherit;
`;

export const NavbarItem: FC<{ href: string }> = ({ children, href }) => {
  return (
    <NavbarAnchor
      whileHover={{ color: Color.danger, scale: 1.2 }}
      href="#about"
      onClick={(e) => {
        e.preventDefault();
        document.querySelector(href)?.scrollIntoView({
          behavior: "smooth",
        });
      }}
    >
      {children}
    </NavbarAnchor>
  );
};
